.Bars {
  /* position: relative;  */
  display: inline-block;
  font-size: 16px;
  display: flex;
  margin-top:10% ;
  margin-left: 20%;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
  width: 55rem;
  backdrop-filter: blur(2px);
}
* img {
  cursor: pointer;
}

.com {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0.1rem;
  padding: 0px;
  height: 2rem;
  width: auto;
}
.Robot_Framework{
  background-color: gray;
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Web {
  background-color: rgb(57, 172, 143);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.MERN {
  background-color: rgb(32, 181, 197);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Java {
  background-color: rgb(199, 90, 90);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Android {
  background-color: #a4c639;
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Flutter {
  background-color: rgb(39, 128, 196);
