}
.Social {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Social img {
  margin: 1rem;
  height: 1.5rem;
}

.SocialSite:hover {
  border-radius: 50%;
  /* box-shadow: 0px 00px  10px rgba(0, 247, 255,0.8);   */
  box-shadow: 0px 00px 10px white;
}
@keyframes fading {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
@keyframes width_300 {
  0% { width: 0px; }
  100% { width: 300px; }
}
@keyframes width_200 {
  0% { width: 0px; }
  100% { width: 200px; }
}
@media screen and (max-width: 450px) {
  .Bars {
    left: 3.5rem;
    top: 8rem;
    display: flex;
    width: 18rem;
  }
  .Social {
    display: flex;
    padding: 0.5rem;
    justify-content: space-evenly;
  }
}
