*{
    margin: 0;
    padding:0; 
    color: #fff;
    user-select: none;  
    text-decoration: none; 
    overflow: hidden !important;  
    cursor: none;
  }
 
.Body{  
    height:100vh ;
    width: 100vw ; 
    overflow: scroll;
}
#Cursor{
  position: absolute;
  height: 20px;
  width: 20px; 
  background-color: rgba(0, 0, 0, 0.808);
  opacity: 0.8;
  border: solid  rgba(255, 255, 255, 0.385);
  border-radius:50%;
  transition: 0 ease-in-out;
  z-index: 0;
  cursor: none;
}

.Clicked{
  border-width: 0.5rem;
  /* background-color: red !important; */
  box-shadow: 0 0 15px rgb(0, 247, 255); 
}

@media screen and (max-width:450px){
  #Cursor{
    display: none;
  }
} 
