.About {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 85vh;
  width: fit-content;
  margin: auto;
  padding: 1rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
}

.List {
  display: flex;
}
.Intro {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 0.5rem;
  width: fit-content;
  width: 50rem;
  text-align: center;
  text-shadow: 0px 0px 2px #fff;
}
.Attribute {
  background: rgba(255, 255, 255, 0.1);
  margin: 1.5rem;
  padding: 1rem;
  text-align: center;
  min-width: 15rem;
  min-height: 15rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 1rem;
  height: 10rem;
}

::-webkit-scrollbar {
  display: none;
}
/*  */

.Education {
  text-align: left;
  font-size: 0.8rem;
  font-weight: 900;
  line-height: 2rem;
  color: black !important;
  line-height: 1.7;
}

.Skills {
  text-align: center;
  font-size: 0.8rem;
  line-height: 2rem;
  display: flex;
  flex-direction: column;
}
.Projects {
  text-align: center;
  font-size: 0.8rem;
  font-weight: 900;
  line-height: 2rem;
  display: flex;
  flex-direction: column;
}
* a {
  cursor: pointer;
  z-index: 2;
}
.Heading {
  font-weight: 900;
}
.Line {
  height: 0.1rem;
  width: 7rem;
  background-color: white;
  box-shadow: 0px 0px 10px white;
}

.Myphoto {
  position: relative;
  height: 10rem;
  width: 10rem;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}
.Myphoto img {
  height: 10rem;
  width: 9.5rem;
  -webkit-user-drag: none;
}
.favLine {
  position: relative;
  /* top: 1rem; */
  padding: 1rem;
  font-weight: 500;
  color: rgb(248, 248, 248);
  text-shadow: 0 0 20px white;
}

.Attribute .description {
  width: 100%;
  display: block;
  height: 100%;
  overflow: scroll !important;
}

/*  MediaQuery.. */
@media screen and (max-width: 450px) {
  * {
    overflow: scroll !important;
  }
  .Myphoto {
    margin: 1.5rem !important;
    margin-top: 0px !important;
    height: 40% !important;
    width: 40% !important;
  }
  .Attribute {
    height: fit-content;
  }
  .Myphoto img {
    height: 100% !important;
    width: 100% !important;
  }
  .Intro,
  .Myphoto {
    width: fit-content;
    font-size: 80%;
    margin: 0;
  }
  .About {
    max-width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: start;
    height: fit-content;
    width: fit-content;
    overflow: scroll !important;
  }
  .About * {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .List {
    height: 40vh;
    display: block;
    width: 100%;
    overflow: scroll !important;
  }
  .Line {
    height: 0.1rem;
    max-width: 8rem;
    background-color: white;
    box-shadow: 0px 0px 10px white;
    margin: 0.5rem;
  }

  .favLine {
    color: rgb(248, 248, 248);
    text-shadow: 0px 0px 10px rgb(255, 255, 255);
    padding: 1rem;
    font-family: "Perpetua";
    font-size: 80%;
  }
  .Education {
    font-size: 80%;
  }
  .Skills * {
    background-color: rgba(0, 0, 0, 0.542);
    border-radius: 1rem;
    height: fit-content;
    margin: 0.4rem;
    font-size: 80%;
  }

  .Projects * {
    background-color: rgba(0, 0, 0, 0.542);
    border-radius: 1rem;
    height: fit-content;
    margin: 0.4rem;
    font-size: 80%;
  }
  .Attribute::-webkit-scrollbar {
    display: visible;
  }
