.HomePage {
  font-family: monospace, ui-monospace;
  font-size: 30px;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: inline-flex;
  justify-content: center; 
}

#HomeContent {
  position: relative;
  display: inline;
  height: auto;
  width: auto;
  top: 7rem;
  /* left: 15rem; */
  font-weight: 600;
}
#HomeContent p {
  width: 70%;
  font-size: 20px;
}
.desc {
  display: inline-block;
  position: relative;
  text-align: center;
  left: 10rem;
  top: 1.9rem;
  font-size: 18px;
}

@media screen and (max-width: 450px) {
  .HomePage * {
    margin: 0 !important;
    left: 0 !important;
    top: 0 !important;
  }
  .HomePage {
    display: block;
    padding: 0 !important;
    height: 100vh;
    width: 100vw;
    overflow: scroll !important;
  }
  #HomeContent {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 80%;
    font-size: 80%;
    align-items: center;
  }

